





























































import Vue from 'vue'
import { defineComponent, reactive, ref, computed } from "@vue/composition-api";
import { component } from 'vue/types/umd';
type Props = {
    lists: Array<String>,
    imgSrc:String,
    title:String,
    description : String,
    githubURL:String,
    demoURL:String
};
export default defineComponent({
    props:{
        lists:{
            type:Array
        },
        imgSrc:{
            type:String,
            required:true
        },
        title:{
            type:String,
            required:true
        },
        description:{
            type:String,
            required:true
        },
        githubURL:{
            type:String,
        },
        demoURL:{
            type:String,
        }
    },
    setup(props:Props){
        const links = ref({
            img:props.imgSrc,
            github:props.githubURL,
            demo:props.demoURL
        });
        const text = ref({
            head:props.title,
            body:props.description
        });
        const items = props.lists;
        return {
            links,
            text,
            items
        }
    }
})
