

































import Vue from 'vue'
import { defineComponent, reactive, ref, computed } from "@vue/composition-api";
import { component } from 'vue/types/umd';

// 引数が特別な配列のため、型を指定
// 本来はクラスを別に作成した方が良い
/**
 * WorkListの引数配列
 */
type WorkInfo = {
    name:String,
    description:String,
    url:String
};

// 引数型の明示
type Props = {
    lists: Array<WorkInfo>
};
export default defineComponent({
    props:{
        lists:{
            type:Array
        },
    },
    setup(props:Props){
        // 引数をそのまま取り込む
        const works = computed(()=>{
            return props.lists;
        });
        return {
            works
        }
    }
})
