

















import Vue from 'vue'
import { defineComponent, reactive, ref, computed } from "@vue/composition-api";
type Props = {
    aHref: String,
    imgName:String,
    pText : String
};
export default defineComponent({
    props:{
        aHref:{
            type:String
        },
        imgName:{
            type:String
        },
        pText:{
            type:String
        }
    },
    setup(props:Props){
        const link = ref({
            aHref : props.aHref,
            imgSrc : props.imgName,
            pText : props.pText
        });
        return {
            link
        }
    }
})
